<template>
    <div class="stepTwo">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="headTitle">{{ userType === 'T' ? '教练认证' : '武馆认证' }}</span>
            <span class="back"></span>
        </div>
        <div class="preViews">
            <div class="preview" v-for="(preView, index) in preViews" :key="index">
                <img :src="preView.fullFile">
            </div>
            <div style="clear: both;"></div>
        </div>
        <div class="upLoad" v-if="preViews.length < 10">
            <label class="getImg" for="myfile" @click="avatar" id="preView"></label>
            <input type="file" accept="image/*" name="myfile" ref="myfiles" style="display: none" multiple @change="upLoadImgs">
        </div>
        <span class="addTitle">添加荣誉证书</span>
        <button class="nextBtn" @click="submit">下一步</button>
    </div>
</template>

<script>
import { uploadImgs } from '../../api/getdata'
// import { getCookie } from '../../../static/js/cookieUtil'
import { Toast } from 'mint-ui'
export default {
  name: 'coachStep2',
  data () {
    return {
      userType: '',
      preViews: []
    }
  },
  mounted () {
    this.userType = localStorage.getItem('userType')
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    avatar () {
      this.$refs.myfiles.click()
    },
    upLoadImgs () {
      if (this.$refs.myfiles.files.length !== 0) {
        var images = new FormData()
        images.append('scene', 'comm')
        for (let i = 0; i < this.$refs.myfiles.files.length; i++) {
          images.append('files', this.$refs.myfiles.files[i])
        }
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        let data = uploadImgs(images, config)
        data.then(res => {
          console.log(res.data)
          this.preViews = res.data
          console.log(this.preViews)
        })
      } else {
        Toast({
          message: '请先选择上传图片',
          duration: 1000
        })
      }
    },
    submit () {
      this.$router.push('/home/enroll/CoachStep3')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.5rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .preview img,
  .back img {
    width: 100%;
    height: 100%;
  }

  .getImg {
    display: block;
    width: 3.73333rem;
    height: 4.73333rem;
    margin: 0 auto;
    background: #f5f5f5;
    border: none;
    outline: none;
    background-image: url('../../assets/img/addUpload.png');
    background-position: center;
    background-size: 1.2rem 1.2rem;
    background-repeat: no-repeat;
  }

  .nextBtn {
    width: 8.77333rem;
    height: 1.33333rem;
    border-radius: 0.15rem;
    background: #ffd200;
    border: none;
    outline: none;
    margin-top: 0.5rem;
    font-weight: 400;
  }

  .addTitle {
    display: inline-block;
    margin-top: 0.906667rem;
  }

  .preViews {
    width: calc(100% - 0.8rem);
    position: absolute;
    top: 2rem;
    left: 0.4rem;
  }

  .preview {
    float: left;
    width: 2.6rem;
    height: 2.6rem;
    margin: 0 0.2rem;
    border: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .upLoad {
    margin-top: 8rem;
  }
</style>



